import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { NewRequirementPage } from './new-requirement.page';

const routes: Routes = [
  {
    path: '',
    component: NewRequirementPage
  },
  {
    path: 'add-client',
    loadChildren: () => import('./add-client/add-client.module').then( m => m.AddClientPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class NewRequirementPageRoutingModule {}
